/* WIDGETS
========================================================================== */

.widgets {
    [class*='col'] {
        padding-right: 5px;
        padding-left: 5px;
    }
}

.widgets.row {
    margin-right: -5px;
    margin-left: -5px;
    .row {
        margin-right: -5px;
        margin-left: -5px;
    }
}

.widget {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
    &:before {
        content: '';
        display: block;
    }
}

.widget_content {
    color: $white;
    position: absolute;
    top: 0;
    right: 5px;
    bottom: 0;
    left: 5px;
    background: $white;
    padding: $spacer;
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: flex-start;
    overflow: hidden;
    padding-top: 55px;
    color:$gray;
    > *:last-child {
        margin-bottom: 0;
    }
    a {
        color: inherit;
    }
}
.widget_content--contrastBlue {
    background:#48b0f7;
    color:$white;
    a {
        color: $white;
    }
}
.widget_content--contrastAqua {
    background:#10cfbd;
    color:$white;
    a {
        color: $white;
    }
}
.widget_content--contrastPurple {
    background:#6d5cae;
    color:$white;
    a {
        color: $white;
    }
}
.widget_content--contrastGray {
    background:#939393;
    color:$white;
    a {
        color: $white;
    }
}


.widget_content--img {
    justify-content: flex-end;
    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        height: 200px;
        z-index: 1;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.65) 100%);
    }
    color:$white;
    a {
        color: $white;
    }
}

.widget--1-1:before {
    padding-top: calc(100% - 5px);
}

.widget--2-1:before {
    padding-top: calc(50% - 10px);
}

.widget--3-2:before {
    padding-top: calc(75% - 10px);
}

.widget_heading {
    font-weight: 300;
    font-size: 20px;
    position: relative;
    z-index: 2;
}

.widget-heading--sm {
    font-size: 16px;
}

.widget_topBar {
    font-size: 11px;
    text-transform: uppercase;
    font-weight: bold;
    position: absolute;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    //align-self: stretch;
    top: $spacer;
    right: $spacer;
    left: $spacer;
    align-items: center;
}
.widget_indicator {
    position:relative;
    padding:$spacer/4 $spacer/2;
    overflow:hidden;
    /*&:before {
        content:'';
        top:0;
        left:-1px;
        bottom:0;
        width:1px;
        border-right:3px solid;
        opacity:.15;
        position:absolute;
    }*/
    &:after {
        content:'';
        top:0;
        left:0;
        right:0;
        height:1px;
        border-bottom:200px solid;
        opacity:.15;
        position:absolute;
    }

}

.widget_searchBox {
    .searchBox-textField {
        height:25px;
        line-height:25px;
        border-color:lighten($gray-light,30);
    }
    a.searchBox-submit {
        height:25px;
        width:25px;
        line-height:25px;
        background:lighten($gray-light,30);
    }
}

.widget_infoList {
    font-size: 13px;
    list-style: none;
    padding: 0;
    display: flex;
    background: rgba($black, .15);
    position: relative;
    z-index: 2;
    justify-content: space-between;
    li {
        padding: $spacer/4 $spacer/2;
    }
}

.widget_fullLink {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    color: inherit;
    z-index: 3;
}

.widget_newsList {
    flex: 2;
    align-self: stretch;
    margin-right:-$spacer;
    margin-left:-$spacer;
    >li {
        padding-right:$spacer;
        padding-left:$spacer;
    }
    .widget_infoList {
        opacity: .78;
        background: transparent;
        > li {
            padding: 0;
        }
    }
}

.widget_toolList,
.widget_eventList {
    align-self: stretch;
    flex: 2;
}
.widget_toolList {
    margin-right:-$spacer;
    margin-left:-$spacer;
    li {
        background:transparent;
        border-bottom:1px solid $gray-lighter;
        border-radius:0;
        a {
            padding:$spacer $spacer/2;
        }
    }
}
.widget_eventList {
    > li {
        margin-bottom:$spacer*2;
    }

}

.widget_dropdown {
    .dropdown-toggle {
        &:after {
            display: none;
        }
        &:before {
            font-size: 40px;
            @include transition(all 0.2s ease-in-out);
            content: '\00b7 \00b7 \00b7';
            line-height: 0;
            margin-right: 2px;
            display: inline-block;
            vertical-align: -.2em;
            letter-spacing:-5px;
        }
    }
    a.dropdown-item {
      color:$gray;
      font-size:14px;
    }
}
